.fav-custom {
  padding: 10px;
  color: white;
  width: 100%;
}

.fav-custom h6 {
  /* border-bottom: 1px solid black; */
  font-weight: 600;
}

.btn-create {
  width: 25px;
  cursor: pointer;
}

.container-custom2 {
  width: 100%;
  overflow: hidden;
}

.overlay2 {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 15%;
  opacity: 0;
  transition: 0.5s ease;
  background-color: #d5d1d5;
  transform: translateY(-100%);
}

.container-custom2:hover .overlay2 {
  opacity: 0.9;
  transform: translateY(0);
}

.text2 {
  color: white;
  font-size: 20px;
  position: absolute;
  top: 60%;
  left: 90%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.add-ico {
  width: 2rem;
}

.profile-side-header {
  padding: 0 !important;
}

.profile-side-main h4 {
  font-weight: 700;
}

/* profile picture test */

.snip1566 {
  position: relative;
  display: inline-block;
  max-width: 60%;
  width: 100%;
  color: #bbb;
  box-shadow: none !important;
  transform: translateZ(0);
}

.snip1566 *,
.snip1566:before,
.snip1566:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-transition: all 0.3s linear;
  transition: all 0.3s linear;
}

.snip1566:before,
.snip1566:after {
  width: 200px;
  height: 200px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border-radius: 50%;
  content: "";
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  z-index: -1;
  border: 2px solid red;
  border-color: transparent red;
}

.snip1566 img {
  width: 200px;
  height: 200px;
  backface-visibility: hidden;
  vertical-align: top;
  border-radius: 50%;
  padding: 10px;
}

.snip1566 figcaption {
  width: 190px;
  height: 190px;
  position: absolute;
  top: 5px;
  /* bottom: 5px; */
  left: 5px;
  /* right: 5px; */
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.9);
  border-radius: 50%;
}

.snip1566 span {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  font-size: 2rem;
  z-index: 1;
}

.snip1566 a {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
}

.snip1566:hover figcaption,
.snip1566.hover figcaption {
  opacity: 1;
  transform: translateX(0);
}

.snip1566:hover:before,
.snip1566.hover:before,
.snip1566:hover:after,
.snip1566.hover:after {
  border-width: 10px;
}

.snip1566:hover:before,
.snip1566.hover:before {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

.snip1566:hover:after,
.snip1566.hover:after {
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

